// 
import $http from './request.js'
//
export const excellentList = ({pageNum,pageSize}) =>{
	return $http.request({
		url:'/wechat/excellent/list',
		header:{
			"Content-Type":"application/json"
		},
		data:{
			pageNum,
			pageSize
		},
		method:'POST'
	})
	
}

